.checkbox-ios .checkbox-ios-switch, .checkbox-ios-desktop .checkbox-ios-switch {
  box-sizing: border-box;
  width: 46px;
  height: 20px;
  vertical-align: top;
  background: #cecdcd;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 25% / 50%;
  transition: all .2s;
  display: inline-block;
  position: relative;
}

.checkbox-ios .checkbox-ios-switch:before, .checkbox-ios-desktop .checkbox-ios-switch:before {
  content: "";
  width: 18px;
  height: 18px;
  background: #fff;
  border-radius: 50%;
  transition: all .15s;
  display: inline-block;
  position: absolute;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .3);
}

.checkbox-ios input[type="checkbox"], .checkbox-ios-desktop input[type="checkbox"] {
  width: 0;
  height: 0;
  z-index: -1;
  opacity: 0;
  display: block;
  position: absolute;
}

.checkbox-ios input[type="checkbox"]:not(:disabled):active + .checkbox-ios-switch:before, .checkbox-ios-desktop input[type="checkbox"]:not(:disabled):active + .checkbox-ios-switch:before {
  box-shadow: inset 0 0 2px rgba(0, 0, 0, .3);
}

.checkbox-ios input[type="checkbox"]:checked + .checkbox-ios-switch, .checkbox-ios-desktop input[type="checkbox"]:checked + .checkbox-ios-switch {
  background: var(--accent-color);
}

.checkbox-ios input[type="checkbox"]:checked + .checkbox-ios-switch:before, .checkbox-ios-desktop input[type="checkbox"]:checked + .checkbox-ios-switch:before {
  transform: translateX(28px);
}

.checkbox-ios input[type="checkbox"]:not(:disabled) + .checkbox-ios-switch, .checkbox-ios-desktop input[type="checkbox"]:not(:disabled) + .checkbox-ios-switch {
  cursor: pointer;
  border-color: rgba(0, 0, 0, 0);
}

.checkbox-ios-desktop {
